.srv_img_cont_1 {
    width: 100%;
    height: 240px;
    background: #384465;
    position: relative;
    margin-bottom: 155px; }

.srv_img_1 {
    position: absolute;
    bottom: -75px;
    left: 50%;
    transform: translateX(-50%);
    width: 470px;
    height: auto; }

.srv_title {
    font-family: Gilroy-Bold;
    font-weight: normal;
    font-size: 38px;
    line-height: 110%;
    text-align: center;
    margin-bottom: 27px; }

#srv_tags {
    font-size: 16px;
    line-height: 190%;
    width: 100%;
    color: #A3A3A3;
    text-align: center;
    margin: 0 auto 100px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap; }

.tag.dotted {
    margin-left: 44px;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: -24px;
        transform: translateY(-50%);
        width: 4px;
        height: 4px;
        background: #C4C4C4;
        border-radius: 50%; } }

.break {
    flex-basis: 100%;
    height: 0; }

.srv_txt_title {
    font-size: 28px;
    line-height: 110%;
    font-weight: normal;
    font-family: Gilroy-Bold;
    margin-bottom: 30px; }

.srv_txt_block {
    margin-bottom: 90px;
    p {
        margin-bottom: 30px; } }


.ul_point {
    margin-bottom: 30px;
    li {
        padding-left: 28px;
        position: relative;
        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            width: 8px;
            height: 8px;
            background: $accent-color;
            border-radius: 50%; } } }

.srv_list_title {
    font-weight: normal;
    font-family: Gilroy-Bold;
    font-size: 22px;
    line-height: 110%;
    margin-bottom: 30px;
    padding-top: 30px;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: 0px;
        left: 0;
        width: 130px;
        height: 4px;
        background: $accent-color; } }

.srv_right_list {
    li {
        position: relative;
        padding-left: 30px;
        margin-bottom: 15px;
        &:before {
            content: '';
            position: absolute;
            top: 11px;
            left: 0;
            width: 15px;
            height: 1px;
            background: $accent-color; } } }

.srv_bold {
    font-family: Gilroy-Bold; }

.srv_list_link {
    color: $accent-color;
    transition: .3s;
    &:hover, &:active, &:focus {
        color: lighten($accent-color, 10%);
        text-decoration: none; } }

.svr_accent_txt {
    font-size: 20px;
    line-height: 28px;
    font-family: Gilroy-SemiBold; }


.result_sec_1 {
    background: #384465;
    color: #fff;
    border-radius: 16px;
    padding: 60px 70px 85px;
    line-height: 140%;
    margin-bottom: 120px;
    .result_title {
        padding: 0 30px; } }

.res_txt {
    padding: 0 30px;
    font-size: 20px;
    &:not(:nth-last-child(1)) {
        margin-bottom: 40px; } }

.result_title {
    font-size: 34px;
    margin-bottom: 35px; }

.res_links {
    display: flex;
    flex-wrap: wrap;
    margin: 55px 0; }


.res_link {
    height: 240px;
    background: rgba(255, 255, 255, .15);
    border: 1px dashed rgba(19, 31, 65, .3);
    width: 50%;
    display: flex;
    align-items: center;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 20px 20px 20px 50px;
    &:nth-child(2) .res_link_txt {
        max-width: 280px; } }


.res_img {
    max-width: 190px;
    margin-right: 50px; }

.result_img_2 {
    max-width: 100%; }

.res_link_txt {
    margin: 0;
    padding: 0;
    max-width: 200px;
    font-size: 20px;
    line-height: 150%;
    span {
        font-family: Gilroy-Bold; } }

.all_services {
    margin-bottom: 170px; }

.all_serv_title {
    font-size: 28px;
    line-height: 140%;
    font-family: Gilroy-Bold;
    font-weight: normal;
    text-align: center;
    margin-bottom: 60px; }

.all_serv_itm {
    margin-bottom: 30px; }

.all_serv_link {
    display: block;
    overflow: hidden;
    &:hover, &:active, &:focus {
        text-decoration: none; } }


.all_serv_wrap {
    height: 240px; }

.all_serv_txt_container {
    display: flex;
    align-items: center;
    justify-content: flex-start; }

.all_serv_txt {
    margin: 0 0 0 70px;
    max-width: 200px;
    font-size: 28px;
    color: $font-color;
    line-height: 140%;
    font-weight: normal;
    font-family: Gilroy-Bold; }

.all_serv_img_container {
    position: relative; }

.all_serv_img {
    position: absolute;
    top: 70px;
    left: 15px; }

.all_serv_1 {
    background: #f5f5f5; }

.all_serv_2 {
    background: #E1EFC2; }

.all_serv_3 {
    border: 6px solid #F5F5F5;
    height: 240px; }

.srv_img_cont_2 {
    min-height: 325px;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 70px; }

.srv_img_2 {
    max-height: 100%;
    max-width: 100%;
    height: auto;
    width: auto; }

p + .srv_txt_title, ul + .srv_sub_title, ul + .srv_txt_title {
    margin-top: 60px; }

.srv_mt {
    margin-top: 30px; }

.srv_sub_title {
    font-family: Gilroy-Bold;
    font-size: 22px;
    line-height: 140%;
    margin: 40px 0 30px; }

.result_sec_2 {
    background: #F8F8F8;
    padding: 80px 70px 95px;
    margin-bottom: 120px;
    border-radius: 16px;
    font-size: 20px;
    line-height: 140%; }

.result_ul_2 {
    padding-left: 50px;
    margin-top: 40px;
    li {
        position: relative;
        margin-bottom: 14px;
        &:before {
            content: '';
            position: absolute;
            width: 20px;
            height: 1px;
            background: $font-color;
            top: 50%;
            left: -50px;
            transform: translateY(-50%); } } }

.result_img_container_2 {
    display: flex;
    justify-content: center;
    align-items: center; }

.result_sec_3 {
    padding: 80px 215px 50px 100px;
    margin-bottom: 110px;
    background-color: #E1EFC2;
