            background-image: url("data:image/svg+xml,%3Csvg width='16' height='14' viewBox='0 0 16 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.7125 0.896729L5.08161 10.5276L1.28747 6.7335L0 8.02098L5.08161 13.1026L16 2.1842L14.7125 0.896729Z' fill='%230085FF'/%3E%3C/svg%3E%0A");
            background-repeat: no-repeat;
