.ul_checked_title {
    display: block;
    font-family: Gilroy-Bold;
    font-size: 20px;
    margin-bottom: 10px; }

.mt_0 {
    margin-top: 0; }

.srv_txt_block .mb_0 {
    margin-bottom: 0; }

.img_bottom {
    position: relative;
    img {
        position: absolute;
        bottom: -20px; } }

.result_sec_4 {
    padding: 100px 70px 100px 100px; }
