.pre_title {
    margin: 125px 0 20px;
    font-size: 30px;
    line-height: 110%;
    color: $font-color; }

.title {
    font-weight: 600;
    font-size: 72px;
    line-height: 110%;
    color: $font-color;
    margin-bottom: 40px; }

.main_description {
    margin-bottom: 160px; }

.main_projects {
    li {
        background-position: center center; } }

.main_projects_arrow {
    background: #f5f5f5;
    transition: background .3s;
    .arrow_link {
        display: block;
        cursor: pointer;
        transition: color .3s; }
    &:hover, &:active, &:focus {
        background: $font-color;
        .arrow_link {
            color: #fff; }
        .link_arrow {
            fill: #fff; } } }


.project_container {
    position: relative;
    overflow: hidden;
    height: 700px;
    max-height: 700px;
    display: grid;
    grid-column-gap: 30px;
    &.c-black {
        grid-template-columns: 1fr 2fr;
        grid-template-areas: "project_txt project_img";
        .link_arrow {
            fill: $font-color; } }
    &.c-white {
        grid-template-columns: 2fr 1fr;
        grid-template-areas: "project_img project_txt";
        .link_arrow {
            fill: #fff; } }
    &:hover, &:active, &:focus {
        text-decoration: none;
        &.c-white {
            color: #fff; }
        &.c-black {
            color: $font-color; } } }

.project_img_container {
    grid-area: project_img;
    align-self: end;
    max-height: 700px;
    display: flex;
    align-items: center; }

.project_txt {
    grid-area: project_txt; }


.project_img {
    max-width: 100%;
    width: 100%;
    position: relative; }

.project_name {
    font-family: Gilroy-Bold;
    font-size: 36px;
    line-height: 110%;
    margin: 185px 0 20px; }

.project_desc {
    font-size: 20px;
    line-height: 140%;
    margin-bottom: 80px; }

.link_arrow {
    fill: $font-color; }

.c-white, .c-white a {
    color: #fff; }

.c-white .link_arrow {
    fill: #fff; }

.c-black, .c-black a {
    color: $font-color; }

.c-black .link_arrow {
    fill: $font-color; }

.project_link {
    font-family: Gilroy-SemiBold;
    font-size: 18px;
    line-height: 140%;
    &:hover {
        text-decoration: none; } }

a:hover .link_arrow {
    margin-left: 25px; }


.link_arrow {
    width: 28px;
    height: 8px;
    margin-left: 20px;
    transition: .3s; }

.arrow_link {
    color: $font-color;
    font-size: 24px;
    line-height: 105.5%;
    font-family: Gilroy-Bold;
    padding: 43px 0;
    display: inline-block;
    &:hover, &:active, &:focus {
        text-decoration: none;
        color: $font-color; } }

.arrow_down {
    fill: $font-color;
    width: 8px;
    height: 28px;
    margin-left: 20px; }

.more_services_link {
    margin: 50px auto 110px;
    padding: 0;
    transition: .3s;
    &:hover {
        transform: translateY(5px); } }

.about_grid {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: 4fr 8fr;
    grid-template-areas: "about_title about_text" "about_link about_text"; }

.about_section {
    background: #f5f5f5;
    padding: 90px 0;
    .section_title {
        grid-area: about_title;
        margin: 0; }
    .arrow_link {
        grid-area: about_link;
        padding: 0;
        align-self: end; } }

.about_text {
    grid-area: about_text;
    p {
        font-size: 20px;
        line-height: 140%;
        margin-bottom: 40px; }
    p:nth-last-child(1) {
        margin-bottom: 0; } }

.our_clients {
    padding: 130px 0 100px; }

.clients_title {
    font-size: 34px;
    line-height: 105.5%;
    margin-bottom: 70px;
    font-family: Gilroy-Bold; }

.clients_list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    justify-content: space-between;
    align-items: center; }

.client {
    padding: 15px; }

.feedback_section {
    background: #f5f5f5;
    padding: 85px 0; }

.feedback_title {
    font-size: 54px;
    line-height: 120%;
    font-family: Gilroy-Bold;
    margin-bottom: 30px; }

.feedback_text {
    line-height: 140%;
    max-width: 400px;
    margin: 0; }

.feedback_form {
    display: flex;
    flex-direction: column;
    textarea {
        height: 200px; }
    input {
        line-height: 0;
        font-size: 18px; }
    input:not(:nth-last-child(1)), textarea {
        margin-bottom: 20px;
        resize: none;
        padding: 18.5px 30px;
        border-radius: 7px;
        border: 1px solid #EDEDED;
        &::placeholder {
            font-size: 16px;
            line-height: 140%;
            color: #B3B3B3;
            transition: .3s; }
        &:active, &:focus {
            outline: 0;
            &::placeholder {
                opacity: 0; } } } }

.feedback_btn {
    width: 100%;
    height: 60px;
    border-radius: 7px; }
