.project_page_header {
    padding: 130px 0 65px;
    .page_title {
        margin-bottom: 20px; }
    .filter_btn {
        cursor: default;
        &:hover, &:active, &:focus {
            color: #aaa;
            background: #f2f2f2; } } }

.project_subtitle {
    font-family: Gilroy-SemiBold;
    font-weight: normal;
    font-size: 30px;
    color: #949494;
    margin-bottom: 35px; }

.project_desc_section {
    padding: 100px 0 200px;
    line-height: 140%; }


.project_desc_title {
    font-family: Gilroy-SemiBold;
    font-weight: normal;
    font-size: 28px;
    line-height: 140%;
    margin-bottom: 30px; }

.project_desc_text {
    font-size: 18px;
    margin-bottom: 0; }

.project_img_wrap {
    margin-top: 90px; }

.project_img_title {
    font-size: 54px;
    margin-bottom: 55px; }

.project_page_img {
    width: 100%;
    max-width: 100%;
    height: auto; }
