.portfolio_section {
    padding: 130px 0 90px; }

.page_title {
    font-size: 48px;
    font-family: Gilroy-Bold;
    font-weight: normal;
    line-height: 110%;
    margin-bottom: 20px; }

.page_desc {
    font-size: 20px;
    line-height: 140%;
    margin-bottom: 35px; }

.filter_list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap; }

.filter_item {
    margin: 0 10px 10px 0; }

.filter_btn {
    background: #f2f2f2;
    border-radius: 4px;
    border: 0;
    padding: 9px 15px;
    font-size: 14px;
    line-height: 110%;
    color: #aaa;
    transition: .3s;
    display: block;
    cursor: pointer;
    &.active_filter {
        background: $accent-color;
        text-decoration: none;
        color: #fff; }
    &:hover, &:active, &:focus {
        &:not(.active_filter) {
            outline: 0;
            background: darken(#f2f2f2, 10%);
            text-decoration: none;
            color: darken(#aaa, 20%); } } }

.archive_section {
    padding: 100px 0 170px; }

.archive_title {
    font-size: 30px;
    line-height: 110%;
    font-family: Gilroy-Bold;
    font-weight: normal;
    margin-bottom: 50px; }

.yaer_title {
    margin: 20px 0 25px;
    font-size: 26px;
    line-height: 110%; }

.archive_project {
    margin-bottom: 30px; }

.arch_project_name {
    font-family: Gilroy-Bold;
    font-weight: normal;
    font-size: 26px;
    line-height: 110%;
    margin-bottom: 10px;
    display: inline-block;
    &:hover, &:focus, &:active {
        color: lighten($accent-color, 10%);
        text-decoration: none; } }

.arch_project_desc {
    font-size: 16px;
    line-height: 140%;
    margin: 0; }
