.contacts_section + .feedback_section {
    background: #fff; }

.contacts_section {
    padding: 130px 0 75px;
    .page_desc {
        margin-bottom: 60px; } }

.contacts {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 75%; }

.contact_title {
    font-family: Gilroy-Bold;
    font-weight: normal;
    font-size: 36px;
    line-height: 150%; }

.contact_link {
    font-weight: 500;
    white-space: nowrap;
    font-size: 24px;
    line-height: 150%;
    color: $accent-color;
    transition: .3s;
    &:hover, &:active, &:focus {
        text-decoration: none;
        color: lighten($accent-color, 10%); } }
