@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";
@import "../../node_modules/bootstrap/scss/bootstrap-grid.scss";

$accent-color: #0085FF;
$font-color: #121212;

@font-face {
    font-family: Gilroy-Regular;
    src: url('../fonts/Gilroy-Regular.ttf'); }

@font-face {
    font-family: Gilroy-Bold;
    src: url('../fonts/Gilroy-Bold.ttf'); }

@font-face {
    font-family: Gilroy-SemiBold;
    src: url('../fonts/Gilroy-SemiBold.ttf'); }

* {
    box-sizing: border-box; }

body {
    font-family: Gilroy-Regular;
    font-size: 18px;
    color: $font-color; }

ul {
    padding: 0;
    margin: 0;
    list-style: none; }

img {
    &:hover, &:active, &:focus {
        outline: 0; } }

.accent_btn {
    line-height: 0;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    font-family: Gilroy-Bold;
    background: $accent-color;
    color: #fff;
    width: 170px;
    height: 40px;
    transition: background .3s;
    &:hover, &:active, &:focus {
        outline: 0;
        background: lighten($accent-color, 10%); } }

.section_title {
    font-family: Gilroy-Bold;
    font-weight: normal;
    font-size: 48px;
    line-height: 105.5%;
    margin: 80px 0 70px; }

.header {
    height: 95px;
    position: relative; }

.menu {
    height: 95px;
    padding: 0 60px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f3f3f3;
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    z-index: 100; }

.menu_part {
    display: flex;
    align-items: center; }

.logo {
    height: 42px;
    width: auto; }

.menu_desc {
    margin: 0 0 0 25px;
    font-size: 16px;
    color: #cfcfcf; }

.menu_items {
    display: flex;
    align-items: center;
    li>a {
        display: block;
        margin-right: 40px;
        padding: 34px 0;
        font-size: 16px;
        color: #a3a3a3;
        position: relative;
        transition: color .3s; }
    li>a:hover, li>a:active, li>a:focus {
        outline: 0;
        text-decoration: none;
        color: $font-color; }
    a.active_link:before {
        content: '';
        position: absolute;
        height: 1px;
        width: 100%;
        background-color: $font-color;
        bottom: -2.5px;
        left: 0; } }

.footer {
    padding: 50px 0 25px;
    background: #090909; }

.footer_container {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: 2fr 8fr 2fr;
    grid-template-areas: "logo menu inst" "logo contacts inst" "logo copyright inst"; }

.footer_logo_wrap {
    grid-area: logo; }

.footer_nav {
    grid-area: menu; }

.footer_contacts {
    grid-area: contacts; }

.copyright_wrap {
    grid-area: copyright; }

.footer_inst {
    grid-area: inst;
    justify-self: end;
    padding-top: 9px;
    white-space: nowrap; }

.footer_menu {
    display: flex;
    align-items: center;
    padding-top: 9px;
    margin-bottom: 65px;
    li>a {
        font-family: Gilroy-SemiBold;
        font-weight: normal;
        font-size: 18px;
        color: #fff;
        margin-right: 40px;
        transition: .3s;
        &:hover, &:active, &:focus {
            color: #cfcfcf;
            text-decoration: none; } } }

.inst_link {
    text-decoration: underline;
    color: #fff;
    font-family: Gilroy-SemiBold;
    font-weight: normal;
    transition: .3s;
    &:hover, &:active, &:focus {
        color: #cfcfcf;
        .inst_icon {
            fill: #cfcfcf; } } }

.inst_icon {
    width: 30px;
    height: 30px;
    fill: #fff;
    transition: .3s;
    margin-right: 10px; }

.footer_contacts {
    display: flex;
    align-items: center;
    margin-bottom: 15px; }

.footer_contact {
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 60px;
    margin-right: 30px;
    overflow: hidden;
    transition: .3s;
    a {
        color: #fff;
        font-size: 16px;
        padding: 12px 30px;
        display: block;
        &:hover, &:active, &:focus {
            color: #fff;
            text-decoration: none; } }
    &:hover, &:active, &:focus {
        border: 1px solid rgba(255, 255, 255, 1); } }

.contact_icon {
    fill: #fff;
    width: 20px;
    height: 20px;
    margin-right: 10px; }

.copyright, .copyright_link {
    font-size: 14px;
    color: rgba(255, 255, 255, .3);
    opacity: 0.3; }

.copyright_link {
    text-decoration: underline;
    transition: .3s;
    &:hover, &:active, &:focus {
        color: #fff; } }

.lock {
    overflow: hidden; }


#burger {
    display: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 30px;
    width: 30px;
    height: 18px;
    cursor: pointer;
    &:before, &:after, span {
        content: '';
        position: absolute;
        height: 2px;
        background: #111;
        border-radius: 2px;
        right: 0;
        width: 100%;
        transition: .3s; }
    &:before {
        top: 0; }
    &:after {
        bottom: 0; }
    span {
        top: 50%;
        transform: translateY(-50%); }
    &.active {
        &:before {
            width: 100%;
            top: 50%;
            transform: rotate(-45deg) translateY(-1px); }
        &::after {
            width: 100%;
            bottom: 50%;
            transform: rotate(45deg) translateY(1px); }
        span {
            opacity: 0; } } }

#overlay {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .8);
    display: none;
    overflow: auto; }

.modal {
    position: relative;
    z-index: 1100;
    width: 770px;
    max-width: calc(100% - 30px);
    background: #fff;
    display: none;
    margin: 140px auto 40px; }

#modal_feedback {
    padding: 60px 70px 70px; }

#modal_thank {
    text-align: center;
    padding: 110px 70px 120px; }

.modal_title {
    font-family: Gilroy-Bold;
    font-size: 38px;
    line-height: 120%;
    margin-bottom: 15px; }

.modal_desc {
    font-size: 18px;
    line-height: 140%;
    margin-bottom: 35px; }

.thank_img {
    margin-bottom: 45px;
    width: 230px;
    height: auto; }

.close_modal {
    position: absolute;
    cursor: pointer;
    right: -40px;
    top: -40px;
    height: 30px;
    width: 30px;
    &:hover {
         &:before, &:after {
             background: #fff; } }
    &:before, &:after {
        content: '';
        position: absolute;
        background: #c5c5c5;
        height: 1px;
        width: 30px;
        transition: background .3s;
        top: 50%;
        left: 0; }
    &:before {
        transform: rotate(-45deg) translateY(-0.5px); }
    &:after {
        transform: rotate(45deg) translateY(0.5px); } }


@import "includes/index";
@import "includes/portfolio";
@import "includes/services";
@import "includes/contacts";
@import "includes/about";
@import "includes/project";
@import "includes/service_page";
@import "includes/media";
