.services_section {
    padding: 130px 0 170px;
    .page_desc {
        margin-bottom: 75px; } }

.services_grid,
.services_grid_full {
    display: grid;
    grid-template-rows: 1fr;
    grid-gap: 30px;
    grid-template-columns: 1fr 1fr; }

.services_grid {
    grid-template-areas: "service_1 service_3" "service_2 service_3"; }

.services_grid_full {
    grid-template-areas: "service_1 service_3" "service_2 service_3" "service_4 service_4" "service_5 service_6"; }

.service {
    padding: 65px 40px 40px 70px;
    min-height: 470px;
    display: block;
    color: $font-color;
    transition: .3s;
    &:hover, &:active, &:focus {
        text-decoration: none;
        color: inherit; } }

.service_title {
    font-size: 28px;
    line-height: 105.5%;
    font-family: Gilroy-SemiBold;
    margin-bottom: 20px; }

.service_desc {
    font-size: 18px;
    line-height: 140%;
    color: #696969;
    max-width: 385px;
    margin: 0; }

.service_1 {
    background: #f5f5f5;
    grid-area: service_1;
    .service_img {
        margin-top: 35px; }
    &:hover {
        background: #e8e8e8; } }

.service_2 {
    border: 6px solid #F5F5F5;
    padding: 59px 34px 34px 64px;
    grid-area: service_2;
    .service_desc {
        max-width: 265px; }
    .service_img {
        margin-top: 60px; }
    &:hover {
        border-color: #E8E8E8; } }

.service_3 {
    background: #E1EFC2;
    grid-area: service_3;
    .service_img {
        margin-top: 48px; }
    &:hover {
        background: #d7eaae; } }

.service_4 {
    background: #F1EFF8;
    display: grid;
    column-gap: 30px;
    grid-template: 1fr / 5fr 7fr;
    padding-right: 70px;
    min-height: 420px;
    grid-area: service_4;
    .service_img {
        margin: 0; }
    &:hover {
        background: #d6ccf2; } }


.service_5 {
    grid-area: service_5;
    .service_img {
        margin-top: 50px; }
    .service_desc {
        max-width: 400px; } }

.service_6 {
    grid-area: service_6; }

.more_services_container {
    display: flex;
    justify-content: center;
    align-items: center; }

.page_desc a {
    color: $accent-color;
    transition: color .3s;
    &:hover, &:active, &:focus {
        text-decoration: none;
        color: lighten($accent-color, 10%); } }

.service_img {
    max-width: 100%; }

