@media (max-width: 1199px) {
    body {
        font-size: 16px; }

    .menu_desc {
        display: none; }

    .clients_list {
        margin: -20px -15px;
        justify-content: space-around; }

    .client {
        padding: 20px 15px; }

    .project_desc {
        font-size: 18px; }

    .arrow_link {
        font-size: 22px;
        padding: 40px 0; }

    .arrow_link {
        font-size: 24px; }

    .service_desc {
        font-size: 16px; }

    .menu_items li>a {
        font-size: 15px; }

    .section_title {
        font-size: 42px;
        margin: 75px 0 60px; }

    .about_text p {
        margin-bottom: 30px;
        line-height: 140%;
        font-size: 18px; }

    .about_section, .feedback_section {
        padding: 70px 0; }

    .feedback_title {
        font-size: 50px; }

    .footer_container {
        width: 100%;
        max-width: 100%;
        grid-template-areas: "logo menu inst" "logo contacts contacts" "logo copyright copyright"; }

    .res_img {
        max-width: 170px;
        margin-right: 25px; }

    .res_link_txt {
        font-size: 16px; }

    .service_4 {
        align-items: center; } }

@media (max-width: 991px) {
    .logo {
        height: auto;
        width: 125px; }

    .menu {
        padding: 0 30px; }

    .menu_items li>a {
        margin-right: 30px; }

    .menu_btn {
        width: 150px; }

    .title {
        font-size: 58px; }

    .pre_title {
        margin: 85px 0 20px; }

    .project_container {
        height: 560px; }

    .service {
        padding: 50px 40px 40px 40px; }

    .service_3 .service_img {
        margin-top: 21px; }

    .more_services_link {
        margin: 0 auto 75px; }

    .arrow_link {
        font-size: 22px; }

    .arrow_down {
        height: 24px;
        margin-left: 14px; }

    .footer_inst {
        padding: 0; }

    .footer_container {
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-areas: "logo logo logo" "menu menu inst" "contacts contacts contacts" "copyright copyright copyright"; }

    .copyright_wrap {
        white-space: nowrap; }

    .footer_menu {
        margin-bottom: 50px; }

    .footer_contacts {
        margin-bottom: 35px; }

    .contacts {
        max-width: 100%; }

    .result_sec_1 {
        padding: 60px 25px 85px; }

    .res_img {
        max-width: 120px; }

    .res_link_txt {
        font-size: 15px; }

    .srv_img_1 {
        max-width: 370px; }

    .result_sec_2 {
        font-size: 16px;
        padding: 60px 40px 75px; }

    .result_title {
        font-size: 28px;
        margin-bottom: 30px; }

    .result_sec_3 {
        padding: 50px 120px 50px 80px; }

    .all_serv_txt {
        margin: 0 0 0 15px;
        position: relative;
        z-index: 10; }

    .all_serv_img {
        left: 35px; }

    .close_modal {
        top: 10px;
        right: 10px;
        &:before, &:after {
            background: #3a3a3a; }
        &:hover {
            &:before, &:after {
                background: $font-color; } } } }

@media (max-width: 767px) {
    .result_sec_2, .result_sec_3, .result_sec_1 {
        display: none; }

    .title {
        font-size: 36px;
        margin-bottom: 40px; }

    .pre_title {
        font-size: 20px;
        margin: 75px 0 20px; }

    .main_description {
        margin-bottom: 90px; }

    .about_grid {
        grid-template-areas: "about_title about_title" "about_text about_text" "about_link about_link"; }

    .about_section {
        .arrow_link {
            margin-top: 10px; }
        .section_title {
            margin-bottom: 10px; } }

    .service {
        overflow: hidden;
        min-height: 420px;
        height: 420px;
        .service_img {
            max-width: 100%; } }

    .services_grid {
        grid-template-rows: 420px;
        grid-template-columns: 1fr;
        grid-template-areas: "service_1" "service_2" "service_3"; }

    .services_grid_full {
        grid-template-rows: 420px;
        grid-template-columns: 1fr;
        grid-template-areas: "service_1" "service_2" "service_3" "service_4" "service_5" "service_6"; }

    .service_2 .service_img {
        margin-top: 20px; }

    .service_4 {
        grid-template: auto / 1fr;
        row-gap: 30px;
        height: auto;
        .service_desc {
            width: auto; } }

    .service_6 .service_img {
        margin-top: 10px; }

    .service_desc {
        width: 100%; }

    .menu, .header {
        height: 90px; }

    .menu_part {
        width: 100%;
        background: #fff; }

    .logo_part {
        justify-content: space-between;
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        z-index: 10;
        padding: 0 35px; }

    .nav_part {
        position: fixed;
        overflow: auto;
        flex-direction: column;
        padding: 80px 35px 40px;
        top: 0;
        left: 0;
        transition: transform .5s;
        height: calc(100% - 90px);
        transform: translateY(-100%);
        &.active {
            transform: translateY(90px); } }

    #burger {
        display: block; }

    .menu_items {
        flex-direction: column;
        width: 100%;
        li>a {
            margin: 0 0 40px 0;
            padding: 0;
            font-size: 30px; } }

    .menu_btn {
        margin-top: 50px;
        width: 100%;
        font-size: 16px;
        padding: 30px 0; }

    .service_desc, .service_2 .service_desc {
        max-width: 100%;
        width: 330px; }

    .service_1 .service_img {
        margin-top: 30px; }

    .service_2 .service_img {
        margin-top: 50px; }

    .service_3 .service_img {
        margin-top: 40px; }

    .section_title,
    .clients_title {
        font-size: 32px; }

    .about_text p {
        font-size: 16px; }

    .our_clients {
        padding: 90px 0; }

    .more_services_link {
        margin-bottom: 35px; }

    .services .section_title {
        text-align: center; }

    .arrow_link {
        font-size: 20px; }

    .feedback_title {
        font-size: 32px;
        line-height: 140%;
        margin-bottom: 20px; }

    .feedback_text {
        font-size: 18px;
        margin-bottom: 45px;
        max-width: 100%; }

    .footer_container {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: "logo logo" "menu inst" "contacts contacts" "copyright copyright"; }

    .footer_contacts {
        flex-wrap: wrap;
        margin: 0; }

    .footer_contact {
        white-space: nowrap;
        margin-bottom: 20px;
        margin-right: 15px; }

    .footer_menu {
        margin-bottom: -5px;
        padding: 0;
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: flex-start;
        li>a {
            display: block;
            margin: 0 0 30px; } }

    .footer_logo {
        margin-bottom: 10px; }

    .copyright_link {
        margin: 0; }

    .copyright_wrap {
        margin: 0; }

    .project_container.c-black,
    .project_container.c-white {
        height: 590px;
        grid-template-columns: 1fr;
        grid-template-areas: "project_txt" "project_img";
        row-gap: 30px; }

    .project_txt {
        width: 100%;
        max-width: 300px;
        .project_link {
            display: none; } }

    .project_name {
        font-size: 32px;
        margin: 58px 0 15px; }

    .project_desc {
        margin: 0; }

    .project_img_container {
        overflow: hidden;
        max-height: 100%; }

    .portfolio_section {
        padding: 85px 0 55px; }

    .archive_section {
        padding: 70px 0 100px; }

    .services_section {
        padding: 85px 0 90px; }

    .contacts_section {
        padding: 85px 0 0; }

    .contact {
        margin: 0 80px 40px 0; }

    .about_page {
        padding: 85px 0 130px; }

    .project_desc_text {
        margin-bottom: 55px; }

    .project_img_title {
        font-size: 36px; }

    .project_page_header {
        padding: 75px 0 65px; }

    .project_subtitle {
        font-size: 18px;
        margin-bottom: 25px; }

    .res_link {
        width: 100%; }

    .res_link_txt {
        font-size: 18px; }

    .srv_txt_title {
        font-size: 24px; }

    .srv_txt_block {
        font-size: 18px; }

    .srv_list {
        margin: 60px 0 80px; }

    .srv_portfolio_list {
        margin: 60px 0 0; }

    .srv_img_cont_2 {
        min-height: 210px;
        padding: 30px 40px; }

    .all_serv_img_container {
        position: static; }

    .all_serv_wrap {
        position: relative; }

    .all_serv_img {
        left: 285px; }

    .all_serv_txt {
        margin: 0 0 0 30px; }

    .modal {
        margin: 80px auto 40px;
        .feedback_form input {
            font-size: 16px; } }

    .modal_title {
        font-size: 28px; }

    .modal_desc {
        font-size: 16px; }

    #modal_feedback {
        padding: 30px 20px 20px; }

    #modal_thank {
        padding: 60px 20px 40px; } }

@media (max-width: 575px) {
    .footer_contact {
        margin: 0 5px 20px; }

    .footer_contacts {
        margin: 0 -10px;
        justify-content: space-around; }

    .feedback_btn {
        font-size: 16px; }

    .footer_container {
        grid-template-columns: 2fr 1fr;
        grid-template-areas: "logo logo" "menu inst" "contacts contacts" "copyright copyright"; }

    .footer_menu li:nth-last-child(1)>a {
        margin: 0; }

    .arch_project_name {
        font-size: 20px; }

    .page_title {
        font-size: 36px; }

    .page_desc {
        font-size: 18px; }

    .service_title {
        font-size: 24px; }

    .service_4 .service_desc {
        width: 100%; }

    .about_page .page_title {
        margin-bottom: 30px; }

    .srv_img_1 {
        max-width: 270px;
        bottom: -55px; }

    .srv_img_cont_1 {
        height: 180px; }

    .srv_list + *>*:nth-child(1),
    .srv_list + .srv_mt {
        margin-top: 0; }

    .srv_portfolio_list + .srv_mt {
        margin-top: 60px; } }

@media (max-width: 424px) {
    .project_container.c-black,
    .project_container.c-white {
        height: 500px; }

    .footer_container {
        grid-template-columns: auto;
        grid-template-areas: "logo" "menu" "inst" "contacts" "copyright";
        * {
            justify-self: center; } }

    .srv_img_cont_2 {
        padding: 20px 15px; }

    .footer_menu {
        align-items: center; }

    .footer_inst {
        justify-self: center;
        margin-bottom: 20px; }

    .all_serv_img {
        left: 180px; }

    .all_serv_txt {
        font-size: 22px;
        margin: 0 0 0 15px;
        max-width: 150px; } }

