.about_page {
    padding: 130px 0;
    .page_title {
        margin-bottom: 50px; } }

.about_text {
    p {
        margin-bottom: 50px;
        font-size: 20px;
        line-height: 150%; }
    a {
        color: $accent-color;
        font-family: Gilroy-Bold;
        transition: .3s;
        &:active, &:hover, &:focus {
            color: lighten($accent-color, 10%);
            text-decoration: none; } } }


.about_subtitle {
    margin: 80px 0 30px; }
